.footer {
    &__top {
        padding: 40px 20px;
        background-color: $white;
        &-title {
            display: flex;
            align-items: center;
            margin-bottom: 18px;
        }   
        &-logo {
            margin: 0;
            @include text-bold;
        }
        h4 {
            margin: 0 0 0 5px;
            @include text-bold;
        }
        &-info {
            padding: 35px 0 0 10px;
            font-weight: 400;
            font-size: 0.75rem;
            line-height: 0.875rem;
            color: $default-text;
        }
        &-cont {
            a{
                text-decoration: none;
            }
        } 
    }
    &__address {
        display: flex;
        margin-bottom: 11px;
        &-icon {
            margin-right: 10px;
        }
        &-text {
            font-weight: 400;
            font-size: 0.875rem;
            line-height: 1rem;
            color: $default-text;
        }
    }
    &__tel {
        display: flex;
        margin-bottom: 16px;
        &-icon {
            margin-right: 10px;
        }
        a {
            color: $default-text;
            text-decoration: none;
        }
    }
    &__social {
        display: flex;
        a {
            margin-right: 10px;
        }
    }
    &__bottom {
        text-align: center;
        font-weight: 400;
        font-size: 0.875rem;
        line-height: 2.25rem;
        color: $link;
        span {
            color: $description-text;
        }
    }
}