header {
    .header {
        display: flex;
        margin-top: 40px;
        padding: 40px 16px;
        background-color: $white;
         &__title {
            margin: 0;
            @include text-bold;
            line-height: 1.3125rem;
         }
         &__logo {
            a {
                @include text-bold;
                line-height: 1.3125rem;
                text-decoration: none;
            }
        }
         .card__photo1 {
            background: url("../img/1.jpg") no-repeat;
            background-size: cover;
            background-position: top left;
            height: 300px;
            width: 300px;
            img {
                 width: 100%;
            }
        }    
    }
    .menu {
        margin: 20px 0 12px 0;
        padding: 16px;
        background-color: $white;
        &__list {
            display: flex;
            padding: 0;
            margin: 0;
            &-item {
                margin-right: 2rem;
                a {
                    font-weight: 400;
                    font-size: 1rem;
                    line-height: 1.1875rem;
                    color: #8D8D8D;
                    text-decoration: none;
                }
            }
        }
        
    }
    .breadcrum__block {
        padding: 1rem;  
        &-item {
            margin-right: 12px;
            a {
                text-decoration: none;
                font-weight: 400;
                font-size: 0.75rem;
                line-height: 0.875rem;
                color: $link;
                margin-right: 12px;
                &:hover {
                    color: $primary;
                    text-decoration: underline;
                }
                &:visited {
                    color: #9B51E0;
                }
            }
            &.active {
                a {
                    color: $default-text;
                }
            }
        }
    }
}
