$primary: #56CCF2;
$link: #2F80ED;
$black: #333333;
$white: #FFFFFF;
$default-text: #4F4F4F;
$description-text: #828282;
$body-background: #D9D9D9;
$borders: #BDBDBD;

@mixin inputs {
    padding: 7px 8px;
    border: 1px solid $borders;
    @include placeholder;
}

@mixin placeholder {
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.1875rem;
    color: $description-text;
}

@mixin text-bold {
    font-weight: 700;
    font-size: 1.125rem;
    color: $black;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
}
body {
    background-color: $body-background;
}

@import 'header.scss';
@import 'main.scss';
@import 'footer.scss';

