main {
    .aside {
        margin: 18px 0 0 0;
        padding: 0;
        &__form {
            background-color: $white;
            padding: 1rem;
            height: auto;
            &-title {
                font-weight: 500;
                font-size: 1rem;
                line-height: 1.1875rem;
                color: $black;
                &::after {
                    content: "";
                    display: block;
                    width: 100%;
                    height: 1px;
                    background-color: $borders;
                    margin: 16px 0;
                }
            }
            &-param {
                margin-bottom: 17px;
                .title {
                    margin-bottom: 11px;
                    display: flex;
                    justify-content: space-between;
                    font-weight: 400;
                    color: $black;
                    font-size: 1rem;
                    line-height: 1.1875rem;
                }
                label {
                    margin: 0 0 11px 9px;
                    color: $default-text;
                    font-weight: 400;
                    font-size: 1rem;
                    line-height: 1.1875rem;
                }
                .checkbox {
                    width: 1rem;
                    height: 1rem;
                    background-color: $primary;
                }
                .text-input {
                    display: flex;
                    justify-content: space-between;
                    input {
                        @include inputs;
                        width: 48%;
                        &::placeholder {
                            @include placeholder
                        }
                    }
                    
                }
                .keyword-input {
                    input {
                        width: 100%;
                        @include inputs;
                        &::placeholder {
                            @include placeholder
                        }
                    }
                    
                }
            }
            &-btn {
                background-color: $primary;
                color: $white;
                width: 100%;
                text-align: center;
                text-transform: uppercase;
                padding: 8px;
                border: none;
                transition: all .6s ease-in-out;
                &:hover {
                    box-shadow: 2px 6px 0px 0px rgba(52, 52, 53, 0.2);;
                }
            }
        }
    }
    .content {
        margin: 18px 0 25px 0;
        padding: 0;
        .container {
            padding: 0;
        }
        &__title{
            padding-left: 20px;
            @include text-bold;
            line-height: 1.625rem;
        }
        &__nav {
            padding-left: 20px;
            display: flex;
            justify-content: space-between;
            margin-bottom: 1rem;
            .pagination__block {
                &-btn {
                    margin-right: 1rem;
                    width: 2rem;
                    height: 2rem;
                    background-color: $white;
                    border: none;
                    a {
                        text-decoration: none;
                        color: $black;
                    }
                    &.active {
                        background-color: $primary;
                    }
                    &:last-child {
                        a {
                            color: $default-text;
                        }
                    }
                }
            }
        }
        .sort__block {
            font-weight: 400;
            font-size: 1rem;
            line-height: 1.1875rem;
            color: $black;
            select {
                @include inputs;
                margin-left: 10px;
                background: $white;
                border: 1px solid $description-text;
            }
        }
        &__cards {
            background-color: $body-background;
            padding-left: 20px;
            &-item {
                background-color: $white;
                margin-bottom: 1rem;
                padding: 1rem;
            }
            .card__photo {
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .card__price {
                text-align: right;
                font-weight: 500;
                font-size: 1rem;
                line-height: 1.1875rem;
                color: $default-text;
            }
            .card__descr {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                &-content {
                    .title {
                        font-weight: 500;
                        font-size: 1.125rem;
                        line-height: 1.3125rem;
                        color: $black;
                    }
                    .text {
                        font-weight: 300;
                        font-size: 0.875rem;
                        line-height: 1rem;
                        color: $description-text;
                    }
                }
                
                &-tags {
                    display: flex;
                    .tag {
                        margin-right: 1rem;
                        padding: 5px 10px;
                        background-color: $body-background;
                        color: $white;
                        font-weight: 300;
                        font-size: 0.75rem;
                        line-height: 0.875rem;
                        text-align: center;
                    }
                }
            }
        }
    }
    .info__block {
        padding-left: 20px;
        font-weight: 400;
        font-size: 1rem;
        line-height: 1.1875rem;
        color: $default-text;
    }
}